/* Normalize */

* {
  box-sizing: border-box;
}

html {
  height: 100%;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  height: 100%;
  margin: 0;
}

main {
  display: block;
}

