/* Main About Block */
.main-about {
    position: relative;
    &::before {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        background: $blue;
        height: 80px;
        width: 100%;
        z-index: 1; }
    &::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        background: url('../img/main/list.svg');
        width: 157px;
        height: 207px;
        z-index: 1; } }
.about-block {
    position: relative;
    font-family: $roboto;
    @include row-flex();
    justify-content: space-between;
    align-items: flex-start;
    min-height: 500px;
    z-index: 3;
    &__text {
        @include col();
        @include size(4.7);
        text-align: justify;
        margin-top: 50px;
        margin-bottom: 50px; }
    &__wrapper {
        position: relative;
        z-index: 3;
        margin-bottom: 50px; }
    &__name {
        display: flex; }
    &__letter {
        font-weight: 900;
        font-size: 5rem;
        line-height: 94px;
        text-transform: uppercase;
        margin-bottom: 25px;
        &_green {
            color: $green; }
        &_blue {
            color: $blue; } }
    &__descr {
        font-size: 16px;
        line-height: 19px;
        color: $grey; }
    &__btn {
        display: flex;
        justify-content: center; }
    &__img {
        @include col();
        @include size(7.6);
        background: url('../img/main/children.svg') no-repeat center;
        background-size: contain;
        height: 591px;
        position: absolute;
        top: -100px;
        right: -50px;
        img {
            width: 100%;
            height: auto;
            object-fit: contain; } }
    &__list {
        position: absolute;
        left: 0;
        bottom: 0; } }
